















import service from '../../service.js';
import {
	mapState,
    mapMutations
} from 'vuex'
export default {
	data() {
		return {
			
		}
	},
	computed: {
	    ...mapState(['hasLogin', 'forcedLogin'])
	},
	methods: {
		...mapMutations(['logout']),
		bindLogout() {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '确认要退出登录吗？',
				success: function (res) {
					if (res.confirm) {
						that.logout();
						service.removeUser();
						/**
						 * 如果需要强制登录跳转回登录页面
						 */
						if (that.forcedLogin) {
						    uni.reLaunch({
						        url: '../login/login',
						    });
						}
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		}
	}
}
